@charset "UTF-8";
@media (min-width: 1200px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl {
    max-width: 1240px;
  }
}
header {
  position: absolute;
  z-index: 1;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1882352941);
}
header .menu-header,
header .logo {
  display: flex;
  align-items: center;
}
header .menu-header {
  justify-content: flex-end;
}
header .menu-header #btn-header-orcamento {
  margin-right: 15px;
}
header .menu-header #icon-search {
  cursor: pointer;
  width: 20px;
  border-right: 1px solid rgba(255, 255, 255, 0.1882352941);
  padding: 30px 25px 30px 0;
  box-sizing: content-box;
}
header .menu-header #secondary-menu-header {
  display: flex;
  align-items: center;
}
header .menu-header #secondary-menu-header nav {
  margin: 0 15px;
}
header .menu-header #icon-menu {
  position: relative;
  width: 30px;
  height: 10px;
  margin-left: 25px;
  cursor: pointer;
}
header .menu-header #icon-menu #icon-menu-top:before,
header .menu-header #icon-menu #icon-menu-bottom:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #fff;
  transition: all ease 0.3s;
}
header .menu-header #icon-menu #icon-menu-top:before {
  top: 0;
}
header .menu-header #icon-menu #icon-menu-bottom:before {
  bottom: 0;
}
header .menu-header #icon-menu:hover #icon-menu-top:before {
  top: -2px;
}
header .menu-header #icon-menu:hover #icon-menu-bottom:before {
  bottom: -2px;
}

#slider-topo {
  width: 100%;
}
#slider-topo .slider-item img,
#slider-topo .slider-item video {
  width: 100%;
  height: 100vh;
  object-fit: cover;
  object-position: top;
}
#slider-topo .container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
#slider-topo .container .slider-text {
  width: 30%;
}
#slider-topo .container .slider-text h2 {
  font-size: 30px;
  line-height: 37px;
  font-weight: bold;
  color: #cae098;
}
#slider-topo .container .slider-text h2::first-line,
#slider-topo .container .slider-text p {
  line-height: 20px;
  color: #fff;
}
#slider-topo .container .slider-text p {
  margin-bottom: 0;
}
#slider-topo .container a {
  margin-top: 20px;
}

* {
  font-family: "Montserrat", sans-serif;
}

.container-fluid {
  padding-left: 0;
  padding-right: 0;
}

.btn {
  font-size: 14px;
  text-decoration: none;
  border: 1px solid;
  border-radius: 30px;
  padding: 6px 18px;
  transition: all ease 0.3s;
}
.btn.btn-large {
  font-size: 16px;
  padding: 8px 32px;
}

.primary-btn {
  background-color: #006635;
  border-color: #006635;
  color: #cae098;
}

.primary-btn:hover {
  background-color: #cae098;
  border-color: #cae098;
  color: #006635;
}

.secondary-btn {
  background-color: #cae098;
  border-color: #cae098;
  color: #006635;
}

.secondary-btn:hover {
  background-color: #006635;
  border-color: #006635;
  color: #cae098;
}

.transparent-btn {
  background-color: transparent;
  color: #fff;
}

.transparent-btn:hover {
  background-color: #fff;
  color: #006635;
}

.slick-next {
  background-color: #fff;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  transition: all ease 0.3s;
}

.slick-next:focus {
  background-color: #fff;
  opacity: 1;
}

.slick-next:hover {
  background-color: #fff;
  opacity: 0.7;
}

.slick-prev {
  display: none;
}

.slick-next {
  right: 5%;
  z-index: 1;
}

.slick-next:before {
  font-family: none;
  font-size: 30px;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
}

.slick-next:before {
  content: "▸";
  color: #006635;
  left: 54%;
}

.menu {
  margin: 0;
  padding: 0;
  display: flex;
  gap: 15px;
}
.menu li {
  position: relative;
  list-style: none;
}
.menu li.menu-item-has-children {
  position: relative;
  padding-right: 12px;
}
.menu li.menu-item-has-children::after {
  content: "▼";
  color: #fff;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  font-size: 8px;
  pointer-events: none;
}
.menu li.menu-item-has-children:hover > .sub-menu {
  display: block;
}
.menu li .sub-menu {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  padding: 10px 0;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  min-width: 150px;
  width: max-content;
}
.menu li .sub-menu li {
  padding: 5px 20px;
}
.menu a {
  display: block;
  font-size: 14px;
  color: #fff;
  text-decoration: none;
}
.menu a:hover {
  color: #fff;
}